
<template>
  <Modal />

  <div class="about">

    <nav>
      <h1>Welcome to my super <strong>VueJS 3</strong> Pokédex</h1>

      <form action="#" @submit.prevent="handleSubmit()" >
        <input type="text" placeholder="Search a Pokémon..." focus>
        <button class="btn btn-primary" type="submit" @click="showModal">Submit</button>
      </form>
      
    </nav>


    <main class="fond-jaune">
      <div class="font-size-plus">
        <button class="btn btn-primary" @click="removeOffset">⇚</button>
        <Dropdown />
        <button class="btn btn-primary" @click="addOffset">⇛</button>
      </div>

      <div class="pokegrid container" v-if="arrPokemons.length > 0">

        <figure v-for="(item, index) in arrPokemons" :key="`${item.name}-${index}`">
          
          <img :src="`${item.sprites.front_default}`" :alt="`Image of ${item.name}`">
          <router-link :to="`/about/${item.id}`" :title="item.name.toUpperCase()">{{ item.name.toUpperCase() }}</router-link>

        </figure>

      </div>

      <div v-else>
        Loading...
      </div>

    </main>


    <div class="font-size-plus fond-jaune">
      <button class="btn btn-primary" @click="removeOffset">⇚</button>
      <button class="btn btn-primary" @click="addOffset">⇛</button>
    </div>

  </div>
</template>

<script>
    import Dropdown from '../components/Dropdown.vue'
    import Modal from '../components/Modal.vue'

    export default ({
        data() {
          return {
            response: [],
            offset: Number(this.$route.params.id) || 0,
            arrPokemons: []
          }
        },
        components: {
          Dropdown,
          Modal
        },
        methods: {

          addOffset() {
            this.offset += 15;
            this.fetchData();
          },

          fetchData() {
            // empty content of the array before printing anything else to the view
            this.arrPokemons = [];
            
            // fetch loop
            for (let i = this.offset + 1; i < this.offset + 16; i++) {
              
              this.response = fetch(`https://pokeapi.co/api/v2/pokemon/` + i).then(response => response.json().then(data => { this.sorta(data) }));

            } // end of for loop

          }, // end of fetch data

          handleSubmit() {
            const INPUT = document.querySelector("input").value;

            let modal = new window.bootstrap.Modal(document.getElementById('exampleModal'), {
              keyboard: false
            });

            // show Pokémon data
            if (INPUT.length > 0) {
              window.location.replace(`/about/${INPUT}`);
            // show empty field warning
            } else {
              console.log("show modal");
              modal.show();
            }

          },

          sortById(a, b) {
              // sort by id
              if (a.id > b.id) {
                  return 1;
              } else if (b.id > a.id) {
                  return -1;
              } else {
                  return 0;
              }
          },

          sorta(myData) {
            this.arrPokemons.push(myData);
            
            this.arrPokemons.sort(this.sortById);
            console.log(this.arrPokemons);
          },

          removeOffset() {
            if (this.offset - 15 >= 0) {
              this.offset -= 15;
              this.fetchData();
            }
          }

        },

        mounted: function() {
          this.fetchData();
        }

    });

</script>

